.userItem-Bar {
    display: flex;
    justify-content: space-between;
    margin: 2% 3% 0% 3%;
  }

  .Avatar--lg {
    border-radius: 10%;
  }
  .Avatar--lg img {
    height: 3.3rem;
    width: 3.3rem;
    padding: 3% 3% 3% 3%;
    border-radius: 10%;
  }
  
  .userItem-content {
    flex: 1;
    padding: 1% 1% 1% 0%;
    margin-left: 1%;
  
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  
    border-bottom: 0.5px solid #dddbdb;
  }
  
  .userItem-date {
    border-bottom: 0.5px solid #dddbdb;
  }
  
  .userItem-name {
    color: #9f9f9f;
    font-size: 88%;
  }
  
  .userItem-text {
    font-size: 98%;
  }