.searchui--bar {
  display: flex;
  margin: 4% 2% 2% 0%;
}

.searchui--bar--icon {
  width: 2em;
  height: 2em;
  margin: 0% 2% 0% 2%;
}

.searchui--bar--button {
  margin-left: 2%;
}

.searchui--content {
  margin-top: 3em;
  margin-bottom: 1em;
}

.searchui--table {
  text-align: center; /* 表格整体内容水平居中 */
  td {
    vertical-align: middle; /* 表格单元格内容垂直居中 */
  }
}

:root {
  --brand-1: #7ed321;
  --brand-2: #7ed321;
  --brand-3: #7ed321;
  --marked-color: #111;
  --btn-primary-color: #111;
}

.Btn {
  color: #39497d;
  border: 0px solid black;
}

