.ChatUi {
    background-color: #f3f3f3;
}

.ChatUiBar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 4% 2% 2% 2%;
    border-bottom: 0.5px solid #dddbdb;
    background-color: white;
  }

.ChatUiBar--Text {
    flex: 1;
    text-align: center;

    margin: 1%;
}

.ChatUiBar--Btn > .Icon {
    height: 1.5em;
    width: 1.5em;
}

#scrollableDiv {
    height: 770px;
    overflow: auto;
    display: flex;
    flex-direction: column-reverse;
    /* flex-direction: column; */
}

.MessageBubble {
    display: flex;
    flex-direction: column; /* 垂直方向排列 */
    justify-content: space-between; /* 上下靠边 */
    align-items: center;
    padding: 2%;
}

.MessageBubble > .Time {
    text-align: center;
    font-size: 0.9rem;
    margin-bottom: 1%;
}

.MessageBubble-content-left {
    align-self: flex-start;
    display: flex;
    margin-right: 3rem;
}

.MessageBubble-content-right {
    align-self: flex-end;
    display: flex;
    margin-left: 3rem;
}

.Bubble-right > .Bubble{
    background-color: #95ec69;
}

.MessageBubble-Avatar-left {
    margin-right: 0.25rem;
    border-radius: 10%;
}

.MessageBubble-Avatar-right {
    margin-left: 0.25rem;
    border-radius: 10%;
}

.Bubble-left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 1 1;
}

.MessageBubble-Name-left {
    color: #383838;
    font-size: small;
    margin-bottom: 0.15rem;
}

.Bubble-right {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    flex: 1 1;
}

.MessageBubble-Name-right {
    color: #383838;
    font-size: small;
    margin-bottom: 0.15rem;
}

.ant-image {
    /* border: #383838; */
    /* border-bottom: 100px solid #dddbdb; */
    display: block;
}

.Bubble.text {
    max-width: 20rem;
}