.ImagesUIBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    padding: 4% 2% 2% 2%;
    border-bottom: 0.5px solid #dddbdb;
    background-color: white;
  }
  
  .ImagesUIBar--Btn > .Icon {
    height: 1.5em;
    width: 1.5em;
  }

.ImageUI-list {
    display: flex;
    flex-wrap: wrap; /* 当子元素超出容器大小时，换行显示 */
    gap: 2px;
}

.preview-ImageItem {
  position: relative;
  width: 96px;
  height: 128px;
  /* display: block; */
  /* border: 1px solid #ccc; */
}

.preview-ImageItem-img {
  object-fit: cover;
}

.preview-ImageItem-icon {
  position: absolute;
  top: 5%;
  left: 5%;
  height: 22px;
  width: 22px;
  color: rgb(224, 222, 222);
}

.endMessage-div {
  display: flex;
  align-items: center;
  justify-content: center;
}