.events {
    margin: 0;
    padding: 0;
    list-style: none;
  }
.events .ant-badge-status {
  width: 100%;
  overflow: hidden;
  font-size: 12px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.notes-month {
  font-size: 28px;
  text-align: center;
}
.notes-month section {
  font-size: 28px;
}

.CalendarLoading {
  margin-top: 40%;
}

.CalendarBar {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 4% 2% 2% 2%;
  border-bottom: 0.5px solid #dddbdb;
  background-color: white;
}

.CalendarBar--Btn > .Icon {
  height: 1.5em;
  width: 1.5em;
}